var sentByMeCountEl = document.getElementById("sentByMeCount");
var receivedMineCountEl = document.getElementById("receivedMineCount");
var receivedOthersCountEl = document.getElementById("receivedOthersCount");
var peerIdDisplayEl = document.getElementById("peerIdDisplay");
var messageListEl = document.getElementById("messageList");
var searchInputEl = document.getElementById("searchInput");
var failedToSendCountEl = document.getElementById("failedToSendCount");
var sentByMe = 0;
var receivedMine = 0;
var receivedOthers = 0;
var failedToSend = 0;
var currentMessages = [];
var currentPeerId;
export function updatePeerIdDisplay(peerId) {
    currentPeerId = peerId;
    if (peerIdDisplayEl) {
        peerIdDisplayEl.textContent = peerId;
    }
}
export function incrementSentByMe() {
    sentByMe++;
    if (sentByMeCountEl)
        sentByMeCountEl.textContent = sentByMe.toString();
}
export function incrementReceivedMine() {
    receivedMine++;
    if (receivedMineCountEl)
        receivedMineCountEl.textContent = receivedMine.toString();
}
export function incrementReceivedOthers() {
    receivedOthers++;
    if (receivedOthersCountEl)
        receivedOthersCountEl.textContent = receivedOthers.toString();
}
export function incrementFailedToSend() {
    failedToSend++;
    if (failedToSendCountEl)
        failedToSendCountEl.textContent = failedToSend.toString();
}
export function addMessageToLog(message, type) {
    currentMessages.push(message);
    renderMessages();
}
export function renderMessages(filterText) {
    if (!messageListEl)
        return;
    messageListEl.innerHTML = "";
    var messagesToRender = filterText
        ? currentMessages.filter(function (msg) {
            var searchTerm = filterText.toLowerCase();
            return (msg.content.toLowerCase().includes(searchTerm) ||
                msg.id.toLowerCase().includes(searchTerm) ||
                msg.senderPeerId.toLowerCase().includes(searchTerm));
        })
        : currentMessages;
    messagesToRender.sort(function (a, b) { return a.timestamp - b.timestamp; });
    messagesToRender.forEach(function (message) {
        var item = document.createElement("div");
        item.classList.add("message-item");
        var typeClass = '';
        var senderPrefix = '';
        if (message.failureInfo) {
            typeClass = 'failed';
            senderPrefix = 'Me (Failed)';
            item.style.backgroundColor = '#ffebee';
            item.style.borderLeft = '4px solid #f44336';
        }
        else if (message.senderPeerId === currentPeerId) {
            typeClass = 'sent';
            senderPrefix = 'Me';
        }
        else {
            typeClass = 'received-other';
            senderPrefix = "Other (".concat(message.senderPeerId.substring(0, 6), "...)");
        }
        item.classList.add(typeClass);
        var idText = document.createElement("p");
        idText.classList.add("message-id");
        idText.textContent = "ID: ".concat(message.id);
        var contentP = document.createElement("p");
        contentP.classList.add("content");
        contentP.textContent = message.content;
        var senderInfoP = document.createElement("p");
        senderInfoP.classList.add("sender-info");
        senderInfoP.textContent = "From: ".concat(senderPrefix);
        var timestampP = document.createElement("p");
        timestampP.classList.add("timestamp");
        timestampP.textContent = new Date(message.timestamp).toLocaleTimeString();
        item.appendChild(idText);
        item.appendChild(senderInfoP);
        item.appendChild(contentP);
        item.appendChild(timestampP);
        // Add failure information if present
        if (message.failureInfo) {
            var failureInfoP = document.createElement("p");
            failureInfoP.classList.add("failure-info");
            failureInfoP.style.color = '#d32f2f';
            failureInfoP.style.fontWeight = 'bold';
            failureInfoP.textContent = "Failed: ".concat(message.failureInfo.error).concat(message.failureInfo.peer ? " (Peer: ".concat(message.failureInfo.peer.substring(0, 12), "...)") : '');
            item.appendChild(failureInfoP);
        }
        messageListEl.appendChild(item);
    });
}
export function getSearchTerm() {
    return searchInputEl ? searchInputEl.value : "";
}
